html {
  text-transform: uppercase;
  --blue-color: #00acdc;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100vw;
  margin: 0;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
  margin-left: 50px;
  display: block;
}

.nav {
  margin: 0 50px;
}

.nav__items {
  margin-right: 20px;
}

.nav__items__noMarging {
  margin: 0;
}

.nav__list {
  white-space: nowrap;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  text-align: center;
  color: #000;
  height: 60px;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.is-active {
  color: var(--blue-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

:hover {
  color: var(--blue-color);
}
